@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.eltdf-comment-holder {
    @include eltdfRelativeHolderLayout();
    padding: 0;
    box-sizing: border-box;

    .eltdf-comment-holder-inner {
        padding: 24px 0 20px;
    }
	
	.eltdf-comment {
		
		&.eltdf-pingback-comment {
			
			.eltdf-comment-text {
				padding: 0;
			}
		}
	}

    .eltdf-comment-list {
        list-style: none;
        position: relative;
        z-index: 150;
        padding: 0;
        margin: 0;

        li {
            @include eltdfRelativeHolderLayout();
            margin: 25px 0 0;
            padding: 45px 0 0;
        }

        > li:first-child {
            margin: 0;
            padding: 0;
            border: 0;
        }

        .children {
            padding: 0 0 0 81px;
        }
    }

    .eltdf-comments-title {
        margin-bottom: 28px;
        position: relative;
        overflow: hidden;

        > * {
            margin: 0;
            color: $default-heading-color;

            &:after{
                position: absolute;
                content: '';
                height: 1px;
                width: 100%;
                top: 50%;
                background-color: $default-border-color;
                @include eltdfTransform(translateY(-50%));
                margin-left: 10px;
            }
        }
    }

    .eltdf-comment-image {
        position: relative;
        display: block;
        top: 0;
        left: 0;
        width: 70px;
        height: 70px;
        float: left;

        img {
            display: block;
            border-radius: 4em;
        }
    }

    .eltdf-comment-text {
        padding: 0 0 0 108px;
        min-height: 82px;

        .eltdf-comment-info {
            @include eltdfRelativeHolderLayout();
            margin: 6px 0 7px;
        }

        .replay,
        .comment-reply-link,
        .comment-edit-link,
        .fa-reply{
            float: right;
            margin: 6px 0 0 10px;
            font-size: 12px;
            color: $first-main-color;

            &:hover {
                color: $default-heading-color;
            }
        }

        .comment-reply-link {

            &:hover {
                &:before {
                    color: #858585;
                }
            }
            &:before {
                content: "\f3e5";
                font-family: 'Font Awesome 5 Free';
                font-weight: 900;
                padding-right: 10px;
            }
        }

        .eltdf-comment-name {
            float: left;
            margin: 0;
        }

        .eltdf-comment-date {
            float: left;
            padding: 0 0 0 13px;
            font-size: 10px;
            text-transform: uppercase;
            position: relative;

            &:before{
                content: '\25CF';
                position: absolute;
                top: -1px;
                left: -2px;
            }
        }

        .eltdf-text-holder p {
            margin: 7px 0;
        }

        .comment-respond {
            margin: 20px 0;

            .comment-reply-title {
                margin: 0 0 10px;
                font-size: 14px;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }
        }

        #cancel-comment-reply-link {
            margin-left: 10px;
            font-size: 12px;
            color: $first-main-color;

            &:hover {
                color: $default-heading-color;
            }
        }
	
	    ul, ol {
		    
		    li {
			    display: list-item;
		    }
	    }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
}

.eltdf-comment-form {
    padding: 0;
    clear: both;
    box-sizing: border-box;
    
    .eltdf-comment-form-inner {
        padding: 57px 0 50px;
    }
    
    .comment-reply-title {
        margin: 0 0 37px;
        position: relative;
        overflow: hidden;

        > * {
            margin: 0;
            color: $default-heading-color;

            &:after{
                position: absolute;
                content: '';
                height: 1px;
                width: 100%;
                top: 50%;
                background-color: $default-border-color;
                @include eltdfTransform(translateY(-50%));
                margin-left: 10px;
            }
        }
    }
    
    .logged-in-as {
        margin: 0 0 8px;
    }
}

@include ipad-portrait {

    .eltdf-comment-holder {
	    
        .eltdf-comment-list {
	        
            .children {
                padding: 0 0 0 40px;
            }
        }
    }
}

@include phone-landscape {
	
    .eltdf-comment-holder {
	    
        .eltdf-comment-list {
	        
            .children {
                padding: 0;
            }
        }

        .eltdf-comment-text {
	        
            .eltdf-comment-date,
            .eltdf-comment-name {
                float: none;
            }
	        
            .replay,
            .comment-reply-link,
            .comment-edit-link {
                float: none;
                margin: 0 10px 0 0;
            }
        }
    }
}

@include phone-portrait {
	
    .eltdf-comment-holder {

        .eltdf-comment-image {
            float: none;
        }

        .eltdf-comment-text {
            padding: 20px 0 0;
            min-height: inherit;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

#respond textarea,
.eltdf-style-form textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
	@extend %input-style;
}

#respond textarea{
    border: 1px solid $default-border-color;
}

.post-password-form input[type='password'] {
	margin-top: 10px;
}

.wpcf7-form-control {
	&.wpcf7-text,
	&.wpcf7-number,
	&.wpcf7-date,
	&.wpcf7-textarea,
	&.wpcf7-select,
	&.wpcf7-quiz {
		@extend %input-style;

		footer & {
			margin: 0;
		}
	}
}

#submit_comment,
.post-password-form input[type='submit'],
input.wpcf7-form-control.wpcf7-submit {
	@include eltdfButtonDefaultStyle();
	@include eltdfButtonMediumParams();
	@include eltdfButtonOutlineColor();
	cursor: pointer;
	
	&:hover {
		@include eltdfButtonOutlineHoverColor();
	}
}

p.form-submit,
.wpcf7-form p {
	margin: 0;
}

p.form-submit{
    float: right;
}

#respond textarea,
.eltdf-style-form textarea,
.wpcf7-form textarea {
	display: block;
	padding: 16px 18px;
}

.eltdf-two-columns-form-without-space {
	
	.wpcf7-form .ajax-loader {
		display: none;
	}
}

#respond .comment-form{
    input[type="text"]{
        width: 47%;
        float: left;
    }

    #email {
        float: right;
    }
}

.eltdf-newsletter{
    position: relative;
    display: inline-block;

    &.light{

        *{
            color: #fff;
        }

        input[type="email"]{
            border-color: #fff;
            width: 470px;

            &:focus {
                color: #fff;
            }
        }

        .eltdf-btn-line{
            background-color: #fff;
        }

        input.wpcf7-form-control.wpcf7-submit {
            font-family: 'Font Awesome 5 Free';
            font-size: 18px;
            color: #fff;

            &:hover {
                color: #fff;
            }
        }

        &.eltdf-item-focus {

            input.wpcf7-form-control.wpcf7-submit {
                color: #fff;
            }
        }
    }

    &.eltdf-item-focus {

        .eltdf-btn-line{
            height: 3px;
            opacity: 1;
            @include eltdfTransition(all .2s ease);
        }
    }

    input[type="email"]{
        border: none;
        border-bottom: 1px solid $default-heading-color;
        padding-left: 0;
        margin: 0;
        max-width: 470px;
    }

    .eltdf-btn-line{
        opacity: 0;
        position: absolute;
        content: '';
        background-color: $default-heading-color;
        height: 1px;
        width: 100%;
        left: 0;
        bottom: 0;
        @include eltdfTransition(all .2s ease);
    }

    input.wpcf7-form-control.wpcf7-submit{
        font-family: 'Font Awesome 5 Free';
        border: none;
        position: absolute;
        font-size: 18px;
        padding: 0;
        line-height: 1em;
        top: 17px;
        right: 0;
        //@include eltdfTransform(translateY(-50%));
        @include eltdfTransition(all .2s ease-in);

        &:hover {
            color: #000;
            background-color: inherit;
            top: 12px;
            @include eltdfTransition(all .2s ease-out);
        }
    }
    
    span.ajax-loader{
        display: none;
    }

    @include phone-landscape {
        &.light {

            input[type="email"] {
                width: 100%;
            }
        }
    }
}

.eltdf-newsletter-sidebar{
    padding: 12px 24px 38px;
    border: 1px solid $default-heading-color;
    margin: 0 24px;
    box-sizing: border-box;
    background-color: #fff;

    input[type="email"]{
        margin: 15px 0 19px;
        border: 1px solid $default-heading-color;
        padding-left: 19px;
    }

    .eltdf-btn-holder{
        position: relative;
        overflow: hidden;
        cursor: pointer; 

        &:after {
            @include eltdfAbsoluteHolderLayout();
            @include eltdfTransform(translateY(75%));
            @include eltdfTransitionTransform(.4s);
            height: 150%;
            background: $default-heading-color;
            content: '';
        }

        &:hover {

            &:after {
                @include eltdfTransform(translateY(-100%));
            }

            .eltdf-btn-line{
                bottom: -3px;
                animation: animateLine 0.2s 0.2s forwards;
                @include eltdfTransition(all .3s ease);
            }

            input.wpcf7-form-control.wpcf7-submit{
                @include eltdfAnimation(colorForward 0.3s);
            }
        }
    }

    .eltdf-btn-line{
        position: absolute;
        content: '';
        background-color: $default-heading-color;
        height: 4px;
        width: 100%;
        left: 0;
        bottom: 0;
        @include eltdfTransition(all .2s ease .2s);
    }

    input.wpcf7-form-control.wpcf7-submit{
        width: 100%;
        z-index: 2;
        position: relative;
        @include eltdfAnimation(colorBackward 0.3s);
        @include eltdfTransition(color .2s);

        &:hover{
            background-color: transparent;
            color: $default-heading-color;
            top: 40%;
            @include eltdfTransition(all .2s ease-out);
        }
    }

    span.ajax-loader{
        display: none;
    }

    @include laptop-landscape-large {
        margin: 0 18px;
        padding-left: 18px;
        padding-right: 18px;
    }
}

.contact-us {
    .wpcf7-form-control.wpcf7-textarea {
        padding: 14px 18px;
        border: 1px solid #4d4d4d;
        margin-bottom: 28px;
    }

    input.wpcf7-form-control.wpcf7-submit {
        float: right;
    }
}
/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.eltdf-owl-slider {
	visibility: hidden;
	
    .owl-stage-outer {
        z-index: 1;
    }
	
	&.eltdf-slider-is-moving {
		.owl-item {
			.eltdf-block-drag-link {
				z-index: -1 !important;
			}
		}
	}
    
    &.eltdf-slider-has-both-nav {
        .owl-nav {
            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 21px); // 21 is is half height of pagination control + top margin
                @include eltdfTransform(translateY($pos));
            }
        }
    }

    &.eltdf-slider-hide-pagination .owl-dots  {
        display: none;
    }
	
	.owl-nav {
		.owl-prev,
		.owl-next {
			position: absolute;
			top: 50%;
			z-index: 3;
			color: $default-heading-color;
			@include eltdfTransform(translateY(-50%));
			@include eltdfTransition(color .2s ease-out);
			
			&:hover {
				color: $first-main-color;
			}
			
			> span {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				font-size: 20px;
				line-height: 50px;
                width: 50px;
                height: 50px;
                color: #fff;
                background-color: $default-heading-color;
                text-align: center;
				
				&:before {
					display: block;
					line-height: inherit;
				}
			}
		}
		
		.owl-prev {
			left: 0;
		}
		
		.owl-next {
			right: 0;
		}
	}
	
	.owl-dots {
		text-align: center;
		margin: 0 0 0;
        position: absolute;
        bottom: 50px;
        left: 50%;
        z-index: 10;
        @include eltdfTransform(translateX(-50%));
		
		.owl-dot {
			display: inline-block;
			vertical-align: middle;
			
			span {
				display: inline-block;
				vertical-align: middle;
				width: 9px;
				height: 9px;
				margin: 0 5px 30px;
				border-radius: 50%;
				background-color: rgba(255,255,255, .5);
				box-sizing: border-box;
				@include eltdfTransition(background-color .2s ease-out);
			}
			
			&:hover,
			&.active {
				
				span {
					background-color: #fff;
				}
			}
		}
	}
	
	.owl-carousel .owl-item img {
		-webkit-transform: translateZ(0); //chrome text cut off fix
	}


    + .eltdf-slider-thumbnail {
        display: inline-block;
        vertical-align: middle;
        list-style: none;
        margin: 20px -10px 0;

        > .eltdf-slider-thumbnail-item {
            float: left;
            padding: 0 10px;
            margin-bottom: 20px;
            box-sizing: border-box;
            line-height: 0;
            cursor: pointer;

            img {
                border: 2px solid transparent;
                @include eltdfTransition(border-color 0.2s ease-out);
            }

            &.active {
                img {
                    border: 2px solid $first-main-color;
                }
            }
        }

        $thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($thumbnail_columns_number) {
            &.eltdf-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {

                .eltdf-slider-thumbnail-item {
                    width: 100% / ($i+2);

                    @include ipad-landscape {
                        width: 33.3333%;
                    }
                }
            }
        }


    }

}
/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.error404 {

    .eltdf-wrapper {
        padding: 0 !important;
    }
    
    .eltdf-content {
	    height: 100vh;
    }
	
	&.admin-bar {
		
		.eltdf-content {
			height: calc(100vh - 32px);
		}
	}

    .eltdf-content-inner {
	    @include eltdfTableLayout();
    }

    .eltdf-page-not-found {
	    @include eltdfTableCellLayout();
        padding: 0 20%;
	    text-align: center;

        .eltdf-404-title {
            margin: 0;
        }
	
	    .eltdf-404-subtitle {
            margin: 35px 0 0;
        }
	    
	    .eltdf-404-text {
		    margin: 10px 0 0;
	    }

        .eltdf-btn {
            margin: 30px 0 0;
	
	        &.eltdf-btn-light-style {
		        color: #585858;
		        background-color: #fff;
		        border-color: #fff;
		
		        &:hover {
			        color: #fff !important;
			        background-color: transparent !important;
			        border-color: #fff !important;
		        }
	        }
        }
    }
	
	@include ipad-landscape {
		
		.eltdf-content {
			height: 100%;
			min-height: 100vh;
		}
		
		&.admin-bar {
			
			.eltdf-content {
				height: 100%;
				min-height: calc(100vh - 32px);
			}
		}
	}

    @include ipad-portrait {

        .eltdf-page-not-found {
            padding: 70px 30px 20px;
        }
    }
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#eltdf-back-to-top {
    position: fixed;
    width: 41px;
    right: 25px;
    bottom: 25px;
    margin: 0;
    z-index: 10000;
    opacity: 0;
    visibility: hidden;
    overflow: hidden;

    &.eltdf-back-to-top-footer {
        position: absolute;
        bottom: 130px;
    }

    &.off {
        opacity: 0;
        visibility: hidden;
        @include eltdfTransition(opacity 0.15s ease, visibility .0s .15s);
    }

    &.on {
        opacity: 1;
        visibility: visible;
        @include eltdfTransition(opacity 0.3s ease, bottom 0s);
    }

    &.eltdf-light {
        > span:not(.eltdf-btn-line) {
            color: #fff;
            border: 1px solid #fff;

            &:after {
                background: #fff;
            }
        }

        .eltdf-btn-line {
            background-color: #fff;
        }
    }

    > span:not(.eltdf-btn-line) {
        position: relative;
        display: block;
        width: 39px;
        height: 39px;
        line-height: 40px;
        font-size: 15px;
        text-align: center;
        color: $first-main-color;
        background-color: transparent;
        border: 1px solid $first-main-color;
        overflow: hidden;
        @include eltdfTransition(color 0.2s ease-in-out, background-color 0.2s ease-in-out, border-color 0.2s ease-in-out);

        &:after {
            @include eltdfAbsoluteHolderLayout();
            @include eltdfTransform(translateY(75%));
            @include eltdfTransitionTransform(.4s);
            height: 150%;
            background: $default-heading-color;
            content: '';
        }

        i {
            z-index: 2;
            position: relative;
            @include eltdfAnimation(colorBackward 0.3s);
        }

        &:hover {
            border: 1px solid currentColor;

            i {
                @include eltdfAnimation(colorForward 0.3s);
            }

            ~ .eltdf-btn-line {
                bottom: -3px;
                animation: animateLine 0.2s 0.2s forwards;
                @include eltdfTransition(all .2s ease);
            }

            &:after {
                @include eltdfTransform(translateY(-107%));
            }
        }
    }

    .eltdf-btn-line {
        height: 3px;
        background-color: $default-heading-color;
        position: absolute;
        bottom: 0;
        width: 100%;
        left: 0;
        @include eltdfTransition(bottom .3s ease .2s);
    }

    span span {
        display: block;
        line-height: inherit;
    }


    @keyframes animateLine {
        0% {
            bottom: -3px;
        }
        100% {
            bottom: 0;
        }
    }

    @keyframes colorForward {
        0% {
            color: inherit;
        }
        50% {
            color: #fff;
        }
        100% {
            color: inherit;
        }
    }

    @keyframes colorBackward {
        100% {
            color: inherit;
        }
        50% {
            color: #fff;
        }
        0% {
            color: inherit;
        }
    }
}
/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

