
.eltdf-st-loader {
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 2100;
    @include eltdfTransform(translate(-50%,-50%));
}

.eltdf-st-loader1 {
    position: relative;
    display: inline-block;
    width: 100%;
    height: 100%;
}

.eltdf-st-loader2 {
    margin: -50% 0 0 -50%;
}

/* rotate circles - start */

.eltdf-rotate-circles {
    width: 60px;
    height: 20px;
    >div {
        background-color: #333;
        display: inline-block;
        width: 12px;
        height: 12px;
        margin: 4px;
        border-radius: 100%;
        opacity: 0;
        -webkit-animation: ball-fall 1.5s ease infinite;
        animation: ball-fall 1.5s ease infinite;
        -webkit-animation-fill-mode: both;
        animation-fill-mode: both;
        &:nth-child(1) {
            -webkit-animation-delay: -200ms;
            animation-delay: -200ms;
        }
        &:nth-child(2) {
            -webkit-animation-delay: -100ms;
            animation-delay: -100ms;
        }
        &:nth-child(3) {
            -webkit-animation-delay: 0ms;
            animation-delay: 0ms;
        }
    }
}

@include keyframes(ball-fall) { 
    0% { 
        opacity:0;
        @include eltdfTransform(translateY(-145%));
    } 
    10% { 
        opacity:.5
    }
    20% { 
        opacity:1;
        @include eltdfTransform(translateY(0));
    }
    80% {
        opacity:1;
        @include eltdfTransform(translateY(0));
    } 
    90% {
        opacity:.5
    }
    100% {
        opacity:0;
        @include eltdfTransform(translateY(145%));
    }
}

/* rotate circles - end */

/* rotate line - start */

.eltdf-rotate-line:before {
    content: '';
    box-sizing: border-box;
    position: absolute;
    top: 50%;
    left: 50%;
    width: 80px;
    height: 80px;
    margin-top: -10px;
    margin-left: -10px;
    border-radius: 50%;
    border: 3px solid transparent;
    border-top-color: currentColor;
    @include eltdfAnimation(rotate-line 1s infinite cubic-bezier(0.6, 0.1, 0, 0.86));
}

@keyframes rotate-line {
    to { transform: rotate(360deg); }
}

/* rotate line - end */

/* roslyn spinner - start */

.eltdf-loading-roslyn {
    box-sizing: border-box;
    top: 50%;
    left: 50%;
    @include eltdfTransform(translate(-50%, -50%));
    margin-top: 120px;
    margin-left: 35px;
    width: 306px;
    height: 138px;
    position: relative;

    .eltdf-loading-mark {
        padding: 65px 0;
        width: 100%;
        margin-top: -10px;
        position: absolute;
        overflow: visible;
        display: flex;
        white-space: nowrap;

        @keyframes marker {
            to { @include eltdfTransform(translateX(100%)); }
        }

        &:before {
            left: -35px;
            top: 49px;
            right: 5px;
            background: #fff;
            content: "";
            width: 100%;
            padding: 0;
            height: 100%;
            z-index: -1;
            position: absolute;
            display: inline-block;
            @include eltdfTransitionTransform(0.3s);
            @include eltdfTransform(translateX(0));
            @include eltdfAnimation(marker 0.7s forwards);
        }

        &:after {
            left: -35px;
            top: 49px;
            right: 5px;
            background-image: url('../img/hover-landing.svg');
            content: "";
            width: 100%;
            padding: 0;
            height: 100%;
            background-repeat: no-repeat;
            background-size: contain;
            z-index: -2;
            position: absolute;
            display: inline-block;
        }
    }

    .eltdf-loading-text {
        margin-top: -10px;
        position: absolute;
        padding: 62px 0;
        font-size: 67px;
        font-family: $additional-font;
        width: 7ch;
        white-space: nowrap;
        overflow: hidden;
        display: flex;
        @include eltdfAnimation(typing 5s steps(7, end) infinite);

        > div {
            display: inline-block;
            width: 4ch;
            margin-top: 6px;
            height: 38px;
            visibility: hidden;
            white-space: nowrap;
            overflow: hidden;
            display: inline-block;
            @include eltdfAnimation(typing-dots 1s 1s steps(3) infinite);
        }

        @keyframes typing { 
          0% { width: 0; } 
          20% { width: 7ch; } 
        }

        @keyframes typing-dots { 
          0% { visibility: visible; width: 0; } 
         80% { visibility: visible; width: 1ch; }
        }

    }
}

/* roslyn spinner - end */

/* pulse - start */

.eltdf-st-loader .pulse {
    width: 32px;
    height: 32px;
    margin: -16px 0 0 -16px;
    background-color: $first-main-color;
    border-radius: 16px;
    -webkit-animation: scaleout 1.0s infinite ease-in-out;
    animation: scaleout 1.0s infinite ease-in-out;
}

@-webkit-keyframes scaleout {

    0% {
        -webkit-transform: scale(0);
    }

    100% {
        -webkit-transform: scale(1);
        opacity: 0;
    }
}

@-moz-keyframes scaleout {

    0% {
        -moz-transform: scale(0);
    }

    100% {
        -moz-transform: scale(1);
        opacity: 0;
    }
}

@-ms-keyframes scaleout {

    0% {
        -ms-transform: scale(0);
    }

    100% {
        -ms-transform: scale(1);
        opacity: 0;
    }
}

@-o-keyframes scaleout {

    0% {
        -o-transform: scale(0);
    }

    100% {
        -o-transform: scale(1);
        opacity: 0;
    }
}

@keyframes scaleout {

    0% {
        transform: scale(0);
        -webkit-transform: scale(0);
    }

    100% {
        transform: scale(1);
        -webkit-transform: scale(1);
        opacity: 0;
    }
}

/* pulse - end */

/* double pulse - start */

.eltdf-st-loader .double_pulse {
    width: 40px;
    height: 40px;
    margin: -20px 0 0 -20px;
    position: relative;
}

.eltdf-st-loader .double_pulse .double-bounce1, .eltdf-st-loader .double_pulse .double-bounce2 {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: $first-main-color;
    opacity: 0.6;
    position: absolute;
    top: 0;
    left: 0;

    -webkit-animation: bounce 2.0s infinite ease-in-out;
    animation: bounce 2.0s infinite ease-in-out;
}

.eltdf-st-loader .double_pulse .double-bounce2 {
    -webkit-animation-delay: -1.0s;
    animation-delay: -1.0s;
}

@-webkit-keyframes bounce {
    0%, 100% {
        -webkit-transform: scale(0.0)
    }
    50% {
        -webkit-transform: scale(1.0)
    }
}

@keyframes bounce {
    0%, 100% {
        transform: scale(0.0);
        -webkit-transform: scale(0.0);
    }
    50% {
        transform: scale(1.0);
        -webkit-transform: scale(1.0);
    }
}

/* double pulse - end */

/* cube - start */

.eltdf-st-loader .cube {
    width: 60px;
    height: 60px;
    margin: -30px 0 0 -30px;
    background-color: $first-main-color;
    -webkit-animation: rotateplane 1.2s infinite ease-in-out;
    animation: rotateplane 1.2s infinite ease-in-out;
}

@-webkit-keyframes rotateplane {
    0% {
        -webkit-transform: perspective(120px)
    }
    50% {
        -webkit-transform: perspective(120px) rotateY(180deg)
    }
    100% {
        -webkit-transform: perspective(120px) rotateY(180deg) rotateX(180deg)
    }
}

@keyframes rotateplane {
    0% {
        transform: perspective(120px) rotateX(0deg) rotateY(0deg);
        -webkit-transform: perspective(120px) rotateX(0deg) rotateY(0deg)
    }
    50% {
        transform: perspective(120px) rotateX(-180.1deg) rotateY(0deg);
        -webkit-transform: perspective(120px) rotateX(-180.1deg) rotateY(0deg)
    }
    100% {
        transform: perspective(120px) rotateX(-180deg) rotateY(-179.9deg);
        -webkit-transform: perspective(120px) rotateX(-180deg) rotateY(-179.9deg);
    }
}

/* cube - end */

/* rotating cubes - start */

.eltdf-st-loader .rotating_cubes {
    width: 32px;
    height: 32px;
    margin: -16px 0 0 -16px;
    position: relative;
}

.eltdf-st-loader .rotating_cubes .cube1, .eltdf-st-loader .rotating_cubes .cube2 {
    background-color: $first-main-color;
    width: 10px;
    height: 10px;
    position: absolute;
    top: 0;
    left: 0;
    -webkit-animation: cubemove 1.8s infinite ease-in-out;
    animation: cubemove 1.8s infinite ease-in-out;
}

.eltdf-st-loader .rotating_cubes .cube2 {
    -webkit-animation-delay: -0.9s;
    animation-delay: -0.9s;
}

@-webkit-keyframes cubemove {
    25% {
        -webkit-transform: translateX(42px) rotate(-90deg) scale(0.5)
    }
    50% {
        -webkit-transform: translateX(42px) translateY(42px) rotate(-180deg)
    }
    75% {
        -webkit-transform: translateX(0px) translateY(42px) rotate(-270deg) scale(0.5)
    }
    100% {
        -webkit-transform: rotate(-360deg)
    }
}

@keyframes cubemove {
    25% {
        transform: translateX(42px) rotate(-90deg) scale(0.5);
        -webkit-transform: translateX(42px) rotate(-90deg) scale(0.5);
    }
    50% {
        transform: translateX(42px) translateY(42px) rotate(-179deg);
        -webkit-transform: translateX(42px) translateY(42px) rotate(-179deg);
    }
    50.1% {
        transform: translateX(42px) translateY(42px) rotate(-180deg);
        -webkit-transform: translateX(42px) translateY(42px) rotate(-180deg);
    }
    75% {
        transform: translateX(0px) translateY(42px) rotate(-270deg) scale(0.5);
        -webkit-transform: translateX(0px) translateY(42px) rotate(-270deg) scale(0.5);
    }
    100% {
        transform: rotate(-360deg);
        -webkit-transform: rotate(-360deg);
    }
}

/* rotating cubes - end */

/* stripes - start */

.eltdf-st-loader .stripes {
    width: 50px;
    height: 60px;
    margin: -30px 0 0 -25px;
    text-align: center;
    font-size: 10px;
}

.eltdf-st-loader .stripes > div {
    background-color: $first-main-color;
    height: 100%;
    width: 6px;
    display: inline-block;
    margin: 0 3px 0 0;

    -webkit-animation: stretchdelay 1.2s infinite ease-in-out;
    animation: stretchdelay 1.2s infinite ease-in-out;
}

.eltdf-st-loader .stripes .rect2 {
    -webkit-animation-delay: -1.1s;
    animation-delay: -1.1s;
}

.eltdf-st-loader .stripes .rect3 {
    -webkit-animation-delay: -1.0s;
    animation-delay: -1.0s;
}

.eltdf-st-loader .stripes .rect4 {
    -webkit-animation-delay: -0.9s;
    animation-delay: -0.9s;
}

.eltdf-st-loader .stripes .rect5 {
    -webkit-animation-delay: -0.8s;
    animation-delay: -0.8s;
}

@-webkit-keyframes stretchdelay {
    0%, 40%, 100% {
        -webkit-transform: scaleY(0.4)
    }
    20% {
        -webkit-transform: scaleY(1.0)
    }
}

@keyframes stretchdelay {
    0%, 40%, 100% {
        transform: scaleY(0.4);
        -webkit-transform: scaleY(0.4);
    }
    20% {
        transform: scaleY(1.0);
        -webkit-transform: scaleY(1.0);
    }
}

/* stripes - end */

/* wave - start */

.eltdf-st-loader .wave {
    width: 72px;
    text-align: center;
    margin: -9px 0 0 -36px;
}

.eltdf-st-loader .wave > div {
    width: 18px;
    height: 18px;
    background-color: $first-main-color;
    margin: 0 3px 0 0;

    border-radius: 100%;
    display: inline-block;
    -webkit-animation: bouncedelay 1.4s infinite ease-in-out;
    animation: bouncedelay 1.4s infinite ease-in-out;
    /* Prevent first frame from flickering when animation starts */
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
}

.eltdf-st-loader .wave .bounce1 {
    -webkit-animation-delay: -0.32s;
    animation-delay: -0.32s;
}

.eltdf-st-loader .wave .bounce2 {
    -webkit-animation-delay: -0.16s;
    animation-delay: -0.16s;
}

@-webkit-keyframes bouncedelay {
    0%, 80%, 100% {
        -webkit-transform: scale(0.0)
    }
    40% {
        -webkit-transform: scale(1.0)
    }
}

@keyframes bouncedelay {
    0%, 80%, 100% {
        transform: scale(0.0);
        -webkit-transform: scale(0.0);
    }
    40% {
        transform: scale(1.0);
        -webkit-transform: scale(1.0);
    }
}

/* wave - end */

/* two_rotating_circles - start */

.eltdf-st-loader .two_rotating_circles {
    width: 40px;
    height: 40px;
    margin: -20px 0 0 -20px;
    position: relative;
    text-align: center;

    -webkit-animation: rotatecircles 2.0s infinite linear;
    animation: rotatecircles 2.0s infinite linear;
}

.eltdf-st-loader .two_rotating_circles .dot1, .eltdf-st-loader .two_rotating_circles .dot2 {
    width: 60%;
    height: 60%;
    display: inline-block;
    position: absolute;
    top: 0;
    background-color: $first-main-color;
    border-radius: 100%;

    -webkit-animation: bounce 2.0s infinite ease-in-out;
    animation: bounce 2.0s infinite ease-in-out;
}

.eltdf-st-loader .two_rotating_circles .dot2 {
    top: auto;
    bottom: 0;
    -webkit-animation-delay: -1.0s;
    animation-delay: -1.0s;
}

@-webkit-keyframes rotatecircles {
    100% {
        -webkit-transform: rotate(360deg)
    }
}

@keyframes rotatecircles {
    100% {
        transform: rotate(360deg);
        -webkit-transform: rotate(360deg)
    }
}

/* two_rotating_circles - end */

/* five_rotating_circles - start */

.eltdf-st-loader .five_rotating_circles {
    width: 60px;
    height: 60px;
    margin: -30px 0 0 -30px;
    position: relative;
}

.eltdf-st-loader .five_rotating_circles .container1 > div, .eltdf-st-loader .five_rotating_circles .container2 > div, .eltdf-st-loader .five_rotating_circles .container3 > div {
    width: 12px;
    height: 12px;
    background-color: $first-main-color;
    border-radius: 100%;
    position: absolute;

    -webkit-animation: bouncedelay 1.2s infinite ease-in-out;
    animation: bouncedelay 1.2s infinite ease-in-out;
    /* Prevent first frame from flickering when animation starts */
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
}

.eltdf-st-loader .five_rotating_circles .spinner-container {
    position: absolute;
    width: 100%;
    height: 100%;
}

.eltdf-st-loader .five_rotating_circles .container2 {
    -webkit-transform: rotateZ(45deg);
    transform: rotateZ(45deg);
}

.eltdf-st-loader .five_rotating_circles .container3 {
    -webkit-transform: rotateZ(90deg);
    transform: rotateZ(90deg);
}

.eltdf-st-loader .five_rotating_circles .circle1 {
    top: 0;
    left: 0;
}

.eltdf-st-loader .five_rotating_circles .circle2 {
    top: 0;
    right: 0;
}

.eltdf-st-loader .five_rotating_circles .circle3 {
    right: 0;
    bottom: 0;
}

.eltdf-st-loader .five_rotating_circles .circle4 {
    left: 0;
    bottom: 0;
}

.eltdf-st-loader .five_rotating_circles .container2 .circle1 {
    -webkit-animation-delay: -1.1s;
    animation-delay: -1.1s;
}

.eltdf-st-loader .five_rotating_circles .container3 .circle1 {
    -webkit-animation-delay: -1.0s;
    animation-delay: -1.0s;
}

.eltdf-st-loader .five_rotating_circles .container1 .circle2 {
    -webkit-animation-delay: -0.9s;
    animation-delay: -0.9s;
}

.eltdf-st-loader .five_rotating_circles .container2 .circle2 {
    -webkit-animation-delay: -0.8s;
    animation-delay: -0.8s;
}

.eltdf-st-loader .five_rotating_circles .container3 .circle2 {
    -webkit-animation-delay: -0.7s;
    animation-delay: -0.7s;
}

.eltdf-st-loader .five_rotating_circles .container1 .circle3 {
    -webkit-animation-delay: -0.6s;
    animation-delay: -0.6s;
}

.eltdf-st-loader .five_rotating_circles .container2 .circle3 {
    -webkit-animation-delay: -0.5s;
    animation-delay: -0.5s;
}

.eltdf-st-loader .five_rotating_circles .container3 .circle3 {
    -webkit-animation-delay: -0.4s;
    animation-delay: -0.4s;
}

.eltdf-st-loader .five_rotating_circles .container1 .circle4 {
    -webkit-animation-delay: -0.3s;
    animation-delay: -0.3s;
}

.eltdf-st-loader .five_rotating_circles .container2 .circle4 {
    -webkit-animation-delay: -0.2s;
    animation-delay: -0.2s;
}

.eltdf-st-loader .five_rotating_circles .container3 .circle4 {
    -webkit-animation-delay: -0.1s;
    animation-delay: -0.1s;
}

/* five_rotating_circles - end */

/* atom - start */

.eltdf-st-loader .atom {
    width: 100px;
    height: 100px;
    margin: -50px 0 0 -50px;
    position: relative;
}

.eltdf-st-loader .atom .ball-1 {
    height: 50%;
    width: 50%;
    left: 50%;
    top: 50%;
    position: absolute;
    z-index: 1;
    -webkit-animation: atom_shrink 4.5s infinite linear;
    -moz-animation: atom_shrink 4.5s infinite linear;
    -o-animation: atom_shrink 4.5s infinite linear;
    animation: atom_shrink 4.5s infinite linear;
}

.eltdf-st-loader .atom .ball-1:before {
    content: '';
    background: $first-main-color;
    border-radius: 50%;
    height: 100%;
    width: 100%;
    position: absolute;
    top: -50%;
    left: -50%;
}

.eltdf-st-loader .atom .ball-2,
.eltdf-st-loader .atom .ball-3,
.eltdf-st-loader .atom .ball-4 {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 0;
}

.eltdf-st-loader .atom .ball-2:before,
.eltdf-st-loader .atom .ball-3:before,
.eltdf-st-loader .atom .ball-4:before {
    content: '';
    height: 20px;
    width: 20px;
    border-radius: 50%;
    background: $first-main-color;
    top: 0;
    left: 0;
    margin: 0 auto;
    position: absolute;
}

.eltdf-st-loader .atom .ball-2 {
    -webkit-animation: atom_zindex 1.5s 0.75s infinite steps(2, end);
    -moz-animation: atom_zindex 1.5s 0.75s infinite steps(2, end);
    -o-animation: atom_zindex 1.5s 0.75s infinite steps(2, end);
    animation: atom_zindex 1.5s 0.75s infinite steps(2, end);
}

.eltdf-st-loader .atom .ball-3 {
    -webkit-transform: rotate(120deg);
    -moz-transform: rotate(120deg);
    -o-transform: rotate(120deg);
    transform: rotate(120deg);
    -webkit-animation: atom_zindex 1.5s -0.25s infinite steps(2, end);
    -moz-animation: atom_zindex 1.5s -0.25s infinite steps(2, end);
    -o-animation: atom_zindex 1.5s -0.25s infinite steps(2, end);
    animation: atom_zindex 1.5s -0.25s infinite steps(2, end);
}

.eltdf-st-loader .atom .ball-4 {

    -webkit-transform: rotate(240deg);
    -moz-transform: rotate(240deg);
    -o-transform: rotate(240deg);
    transform: rotate(240deg);
    -webkit-animation: atom_zindex 1.5s 0.25s infinite steps(2, end);
    -moz-animation: atom_zindex 1.5s 0.25s infinite steps(2, end);
    -o-animation: atom_zindex 1.5s 0.25s infinite steps(2, end);
    animation: atom_zindex 1.5s 0.25s infinite steps(2, end);
}

.eltdf-st-loader .atom .ball-2:before {
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
    -webkit-animation: atom_position 1.5s infinite ease, atom_size 1.5s -1.125s infinite ease;
    -moz-animation: atom_position 1.5s infinite ease, atom_size 1.5s -1.125s infinite ease;
    -o-animation: atom_position 1.5s infinite ease, atom_size 1.5s -1.125s infinite ease;
    animation: atom_position 1.5s infinite ease, atom_size 1.5s -1.125s infinite ease;
}

.eltdf-st-loader .atom .ball-3:before {
    -webkit-animation: atom_position 1.5s -1s infinite ease, atom_size 1.5s -0.75s infinite ease;
    -moz-animation: atom_position 1.5s -1s infinite ease, atom_size 1.5s -0.75s infinite ease;
    -o-animation: atom_position 1.5s -1s infinite ease, atom_size 1.5s -0.75s infinite ease;
    animation: atom_position 1.5s -1s infinite ease, atom_size 1.5s -0.75s infinite ease;
}

.eltdf-st-loader .atom .ball-4:before {
    -webkit-animation: atom_position 1.5s -0.5s infinite ease, atom_size 1.5s -0.125s infinite ease;
    -moz-animation: atom_position 1.5s -0.5s infinite ease, atom_size 1.5s -0.125s infinite ease;
    -o-animation: atom_position 1.5s -0.5s infinite ease, atom_size 1.5s -0.125s infinite ease;
    animation: atom_position 1.5s -0.5s infinite ease, atom_size 1.5s -0.125s infinite ease;
}

@-webkit-keyframes atom_position {
    50% {
        top: 80px;
        left: 80px;
    }
}

@-moz-keyframes atom_position {
    50% {
        top: 80px;
        left: 80px;
    }
}

@-o-keyframes atom_position {
    50% {
        top: 80px;
        left: 80px;
    }
}

@keyframes atom_position {
    50% {
        top: 80px;
        left: 80px;
    }
}

@-webkit-keyframes atom_size {
    50% {
        -webkit-transform: scale(0.5, 0.5);
    }
}

@-moz-keyframes atom_size {
    50% {
        -moz-transform: scale(0.5, 0.5);
    }
}

@-o-keyframes atom_size {
    50% {
        -o-transform: scale(0.5, 0.5);
    }
}

@keyframes atom_size {
    50% {
        transform: scale(0.5, 0.5);
    }
}

@-webkit-keyframes atom_zindex {
    100% {
        z-index: 10;
    }
}

@-moz-keyframes atom_zindex {
    100% {
        z-index: 10;
    }
}

@-o-keyframes atom_zindex {
    100% {
        z-index: 10;
    }
}

@keyframes atom_zindex {
    100% {
        z-index: 10;
    }
}

@-webkit-keyframes atom_shrink {
    50% {
        -webkit-transform: scale(0.9, 0.9)
