/* ==========================================================================
   Default WordPress widgets styles - begin
   ========================================================================== */

.widget {
    margin: 0 0 $widget-bottom-margin;
    padding: 0;

    .eltdf-widget-title-holder {
        margin-bottom: $title-margin;

        .eltdf-widget-title {
            color: $widget-heading-color;
            margin-bottom: 3px;
        }

        .eltdf-separator {
            border-color: $widget-text-color;
            border-bottom-width: 3px;
            width: 30px;
        }
    }

    ul {
        padding: 0;
        margin: 0;
        list-style: none;

        li {
            display: block;
            line-height: $widget-list-item-height;
            margin: 0;
            padding: $widget-list-item-padding 0;

            &:last-child {
                border: 0;
                padding-bottom: 0;
                margin: 0;
            }

            a {
                color: $widget-link-color;

                &:hover {
                    color: $widget-link-hover-color;
                }
            }
        }

        .children,
        .sub-menu {
            margin-top: $widget-list-item-padding;
            padding-left: 10px;
        }
    }

    // widgets with lists and animation on link hover
    &.widget_pages,
    &.widget_archive,
    &.widget_categories,
    &.widget_meta,
    &.widget_recent_entries,
    &.widget_nav_menu {

        .eltdf-widget-title {
            margin-bottom: 0;
        }

        ul {
            li {
                overflow: hidden;
                font-family: $default-heading-font;
                font-weight: 400;
                font-size: 18px;
                color: #fff;

                a {
                    display: inline;
                    overflow: hidden;
                    padding: 0;
                    position: relative;
                    font-family: $default-heading-font;
                    font-size: 18px;
                    font-weight: 400;
                    @include eltdfTransition(padding .2s ease);

                    &:after {
                        content: '\25CF';
                        display: inline-block;
                        left: -7px;
                        position: absolute;
                        speak: none;
                        text-rendering: auto;
                        text-transform: none;
                        top: 50%;
                        font-size: 10px;
                        vertical-align: middle;
                        -webkit-font-smoothing: antialiased;
                        -moz-osx-font-smoothing: grayscale;
                        @include eltdfTransform(translateY(-50%));
                        @include eltdfTransition(left .2s ease-out);
                    }

                    &:hover {
                        padding-left: 15px;

                        &:after {
                            left: 0;
                        }
                    }
                }
            }
        }
    }

    // widgets with lists and w/o animation on link hover
    &.widget_recent_comments,
    &.widget_rss {

        .eltdf-widget-title {
            margin-bottom: 0;
        }
    }

    // calendar
    #wp-calendar {
        text-align: center;
        width: 100%;

        caption {
            margin: 0 0 $widget-list-item-padding;
        }

        th, td {
            padding: 3px 4px;
        }

        td#today {
            color: $widget-text-color;
            background-color: $widget-list-item-background-color;
        }

        tfoot a {
            color: $widget-link-color;

            &:hover {
                color: $widget-link-hover-color;
            }
        }
    }

    // rss
    &.widget_rss {

        .rss-widget-icon {
            display: inline-block;
        }

        .rss-date {
            display: block;
            margin-bottom: $widget-list-item-padding;
        }

        cite {
            display: block;
            margin-top: calc(#{$widget-list-item-padding} / 2);
        }
    }

    // search
    &.widget_search {

        .searchform {
            min-height: 42px;
        }

        .input-holder {
            border: none;
            position: relative;

            &:hover{
                &:after {
                    height: 3px;
                    @include eltdfTransition(all .2s ease-in-out);
                }
            }

            &:after {
                content: '';
                width: 100%;
                left: 0;
                bottom: -43px;
                height: 1px;
                background-color: $default-border-color;
                @include eltdfTransition(all .2s ease-in-out);
                position: absolute;
            }

            input,
            button {
                background-color: transparent;
                border: 0;
                border-radius: 0;
                box-sizing: border-box;
                color: $widget-text-color;
                display: inline-block;
                float: left;
                font-family: $default-text-font;
                font-size: 13px;
                font-weight: inherit;
                height: $widget-search-form-height;
                line-height: calc(#{$widget-search-form-height} - 2px);
                outline: 0;
                position: relative;
                vertical-align: top;
                -webkit-appearance: none;
                @include eltdfTransition(color .2s ease-in-out);
            }

            input.search-field {
                width: calc(100% - #{$widget-search-form-height});
                padding: 0;

                @include eltdfPlaceholder {
                    color: $widget-text-color;
                }
            }

            button {
                background: $widget-list-item-background-color;
                color: $widget-text-color;
                cursor: pointer;
                font-size: 18px;
                width: $widget-search-form-height;
                @include eltdfTransition(color .2s ease-in-out);
                padding: 0;
                float: right;

                &:hover {
                    color: $widget-link-hover-color;
                }

                span {
                    display: block;
                    left: 0;
                    line-height: inherit;
                    position: absolute;
                    top: 0;
                    width: 100%;

                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }
    }

    // block search widget
    &.widget_block {

        &.widget_search {

            .eltdf-search-form {
                min-height: 42px;
            }

            .eltdf-search-form-inner {
                border: none;
                position: relative;

                &:hover{
                    &:after {
                        height: 3px;
                        @include eltdfTransition(all .2s ease-in-out);
                    }
                }

                &:after {
                    content: '';
                    width: 100%;
                    left: 0;
                    bottom: -43px;
                    height: 1px;
                    background-color: $default-border-color;
                    @include eltdfTransition(all .2s ease-in-out);
                    position: absolute;
                }

                input,
                button {
                    background-color: transparent;
                    border: 0;
                    border-radius: 0;
                    box-sizing: border-box;
                    color: $widget-text-color;
                    display: inline-block;
                    float: left;
                    font-family: $default-text-font;
                    font-size: 13px;
                    font-weight: inherit;
                    height: $widget-search-form-height;
                    line-height: calc(#{$widget-search-form-height} - 2px);
                    outline: 0;
                    position: relative;
                    vertical-align: top;
                    -webkit-appearance: none;
                    @include eltdfTransition(color .2s ease-in-out);
                }

                input.eltdf-search-form-field  {
                    width: calc(100% - #{$widget-search-form-height});
                    padding: 0;

                    @include eltdfPlaceholder {
                        color: $widget-text-color;
                    }
                }

                button {
                    background: $widget-list-item-background-color;
                    color: $widget-text-color;
                    cursor: pointer;
                    font-size: 18px;
                    width: $widget-search-form-height;
                    @include eltdfTransition(color .2s ease-in-out);
                    padding: 0;
                    float: right;

                    &:hover {
                        color: $widget-link-hover-color;
                    }

                    span {
                        display: block;
                        left: 0;
                        line-height: inherit;
                        position: absolute;
                        top: 0;
                        width: 100%;

                        &:before {
                            display: block;
                            line-height: inherit;
                        }
                    }
                }
            }
        }
    }

    // search post type
    &.eltdf-search-post-type-widget {
        .eltdf-post-type-search-field {
            border-radius: 3px;
            border-color: $widget-list-item-border-color;
            border-style: $widget-list-item-border-style;
            border-width: $widget-list-item-border-width;
            background-color: transparent;
            box-sizing: border-box;
            color: $widget-text-color;
            display: inline-block;
            float: left;
            font-family: $default-text-font;
            font-size: 12px;
            font-weight: inherit;
            height: $widget-search-form-height;
            line-height: calc(#{$widget-search-form-height} - 2px);
            outline: 0;
            position: relative;
            vertical-align: top;
            -webkit-appearance: none;
            padding: 0 25px 0 20px;
            width: 100%; /* width of loading icon */
            @include eltdfTransition(color .2s ease-in-out);

            @include eltdfPlaceholder {
                color: $widget-text-color;
            }
        }
    }

    // combobox
    &.widget_archive,
    &.widget_categories,
    &.widget_text {

        select {
            background-color: transparent;
            border-color: $widget-list-item-border-color;
            border-style: $widget-list-item-border-style;
            border-width: $widget-list-item-border-width;
            border-radius: 0;
            box-sizing: border-box;
            color: $widget-text-color;
            display: inline-block;
            font-family: $default-text-font;
            font-size: 15px;
            font-weight: inherit;
            height: $widget-search-form-height;
            line-height: calc(#{$widget-search-form-height} - 2px);
            margin-top: $widget-list-item-padding;
            outline: 0;
            padding: 0 0 0 21px;
            position: relative;
            vertical-align: top;
            width: 100%;
            @include eltdfTransition(color .2s ease-in-out);
        }
    }

    // tag cloud
    &.widget_tag_cloud {

        .eltdf-widget-title-holder {
            margin-bottom: 40px;
        }

        .tagcloud{
            &:after{
                content: '';
                clear: both;
                display: block;
                height: 0;
            }
        }

        a {
            border-radius: 0;
            color: $default-heading-color;
            font-weight: 500;
            display: inline-block;
            font-size: 10px !important;
            font-family: $default-text-font;
            letter-spacing: .12em;
            line-height: $widget-tag-cloud-height;
            text-transform: uppercase;
            margin: 0 -1px -1px 0;
            padding: 0 9px;
            border: 1px solid $default-heading-color;
            float: left;
            @include eltdfTransition(all .2s ease);

            &:hover {
                color: #fff;
                background-color: $default-heading-color;
                border-color: $default-heading-color;
                @include eltdfTransition(all .2s ease);
            }
        }
    }

    &.eltdf-recent-post-widget{
        .eltdf-recent-posts{
            .eltdf-rp-item{
                margin: 0 0 10px;
                padding: $widget-list-item-padding;

                a{
                    display: table;
                    width: 100%;

                    .eltdf-rp-image,
                    .eltdf-rp-title{
                        display: table-cell;
                        vertical-align: middle;
                    }

                    .eltdf-rp-title{
                        @include eltdfTransition(color .2s ease-in-out);
                        font-weight: 600;
                    }

                    .eltdf-rp-image{
                        padding-right: 20px;
                        width: 56px;
                        height: 56px;
                        img{
                            display: block;
                        }
                    }

                    &:hover{
                        .eltdf-rp-title{
                            color: $default-text-color;
                        }
                    }
                }
            }
        }
    }

    &.eltdf-blog-list-widget {
        .eltdf-widget-title-holder {
            margin-bottom: 40px;
        }
    }
}