// widgets spacing
$widget-bottom-margin: 30px;
$widget-bottom-padding: 15px;
$widget-inner-padding: 15px;
$title-margin: 24px;

// widget color
$widget-background-color: #eeeeee;

// list items dimensions
$widget-list-item-height: 20px;
$widget-list-item-padding: 9px;

// list items color
$widget-list-item-background-color: #ffffff;

// list items border
$widget-list-item-border-color: $default-border-color;
$widget-list-item-border-width: 1px;
$widget-list-item-border-style: solid;

// text
$widget-text-color: inherit;
$widget-heading-color: #fff;

// links
$widget-link-color: $first-main-color;
$widget-link-hover-color: darken($default-text-color, 35%);

// specific widgets
$widget-search-form-height: 42px;
$widget-tag-cloud-height: 30px;

aside.eltdf-sidebar {
    position: relative;
    z-index: 11;
    margin: 0 0 60px;

    .widget {
        color: $default-text-color;

        &:last-child {
            margin: 0 !important;
        }
    }
}

.wpb_widgetised_column {
    position: relative;

    .widget {

        .eltdf-sidearea-widget-title {
            margin: 0 0 19px;
        }

        &:last-child {
            margin: 0 !important;
        }
    }
}

aside.eltdf-sidebar,
.wpb_widgetised_column {
    @import 'widgets/default-wordpress-widgets-flat';

    .widget {
        .eltdf-widget-title-holder {
            background-color: $default-heading-color;

            .eltdf-widget-title {
                padding: 16px 21px;
                margin: 0;
            }
        }
    }
}