/* ==========================================================================
   Access Press Social Count style start
   ========================================================================== */

.eltdf-accesspress-custom-style .apsc-icons-wrapper {
    position: relative;
    margin: 0;

    .apsc-each-profile {
        position: relative;
        display: block;
        vertical-align: top;
        float: none;
        margin: 0;
        padding: 0;
        margin-bottom: 9px;
        width: auto;
        height: auto;
        text-align: left;
        border: 1px solid $default-heading-color;
        border-radius: 0;

         > a {
            position: relative;
            display: block;
            vertical-align: middle;
            height: 43px;
            font-size: 18px;
            line-height: 43px;
            padding: 0;
            box-sizing: border-box;
            color: $default-heading-color;
            background-color: transparent;
            border: 0;
            border-radius: 0;
            @include eltdfTransition(color .2s ease-out, background-color .2s ease-out);

            .apsc-inner-block {
                position: relative;
                display: block;
                width: 100%;
                vertical-align: middle;
            }

            .fa {
                @include eltdfTransform(none);
                display: inline-block;
                font-family: 'Font Awesome\ 5 Brands';

                &.fa-facebook {
                    &:before {
                        content: "\f39e";
                    }
                }
            }

            .media-name {
                display: none;
            }

            .social-icon {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                text-align: center;
                padding: 0;
                width: 44px;
                min-width: 44px;
                height: 44px;
                color: $default-heading-color;
                border-right: 1px solid $default-heading-color;
                opacity: 1;
                @include eltdfTransition(opacity .2s ease-out);

                &:after{
                    display: none;
                }
            }

            .apsc-count {
                display: inline-block;
                vertical-align: middle;
                opacity: 1;
                min-width: auto;
                color: $default-heading-color;
                font-size: 10px;
                font-weight: 300;
                letter-spacing: .12em;
                text-transform: uppercase;
                font-family: $default-text-font;
                padding: 0 10px;
                @include eltdfTransition(opacity .2s ease-out);
                background-color: transparent;
            }

             &:hover {
                 .apsc-count {
                     background-color: initial!important;
                 }
                 
                 .social-icon {
                     background: #000;
                     color: #fff;
                     border-radius: 0;
                 }
             }

             .apsc-media-type {
                 display: inline-block;
                 color: $default-heading-color;
                 font-size: 10px;
                 font-weight: 500;
                 letter-spacing: .12em;
                 text-transform: uppercase;
                 font-family: $default-text-font;
             }
        }
    }
}
/* ==========================================================================
   Access Press Social Count style end
   ========================================================================== */