// widgets spacing
$widget-bottom-margin: 45px;
$widget-bottom-padding: 15px;
$widget-inner-padding: 15px;
$title-margin: 30px;

// widget color
$widget-background-color: transparent;

// widget border
$widget-border-color: $default-border-color;
$widget-border-width: 1px;
$widget-border-style: solid;

// list items dimensions
$widget-list-item-height: 20px;
$widget-list-item-padding: 15px;

// list items color
$widget-list-item-background-color: #999999;

// list items border
$widget-list-item-border-color: #cccccc;
$widget-list-item-border-width: 1px;
$widget-list-item-border-style: solid;

// text
$widget-text-color: #333333;
$widget-heading-color: #999999;

// links
$widget-link-color: #666666;
$widget-link-hover-color: $first-main-color;

// specific widgets
$widget-search-form-height: 52px;
$widget-tag-cloud-height: 30px;

.eltdf-side-menu {
    @import 'widgets/default-wordpress-widgets-flat';

    @include laptop-landscape-mac {
        .eltdf-social-icons-group-widget{
            margin-bottom: 20px;

            &.eltdf-square-icons .eltdf-social-icon-widget-holder {
                height: 45px;
                width: 45px;
                line-height: 45px;
            }
        }
    }

    .eltdf-news-holder.eltdf-news-skin-light{
        .eltdf-ni-info > div a:hover,
        .eltdf-post-title a:hover {
            color: #fff !important;
        }
    }
}