.eltdf-page-header,
.eltdf-top-bar,
.eltdf-sticky-header,
.eltdf-mobile-header {
    .widget {
        /* To display none */
        &.widget_pages,
        &.widget_archive,
        &.widget_categories,
        &.widget_meta,
        &.widget_recent_entries,
        &.widget_recent_comments,
        &.widget_rss,
        &.widget_calendar,
        &.widget_rss,
        &.widget_archive,
        &.widget_categories,
        &.widget_tag_cloud,
        &.widget_nav_menu,
        &.widget_search {
            display: none !important;
        }
    }
}

.eltdf-mobile-header {
    .widget {
        &.widget_text {
            display: none;
        }
    }
}