  animation: colorForward 0.3s;
}

#eltdf-back-to-top > span:not(.eltdf-btn-line):hover ~ .eltdf-btn-line {
  bottom: -3px;
  animation: animateLine 0.2s 0.2s forwards;
  -webkit-transition: all 0.2s ease;
  -moz-transition: all 0.2s ease;
