// widgets spacing
$widget-bottom-margin: 15px;
$title-margin: 10px;

// list items dimensions
$widget-list-item-height: 20px;
$widget-list-item-padding: 15px;

// list items color
$widget-list-item-background-color: #999999;

// list items border
$widget-list-item-border-color: #fff;
$widget-list-item-border-width: 1px;
$widget-list-item-border-style: solid;

// text
$widget-text-color: #ffffff;
$widget-heading-color: #ffffff;

// links
$widget-link-color: #eeeeee;
$widget-link-hover-color: #fff;

// specific widgets
$widget-search-form-height: 42px;
$widget-tag-cloud-height: 30px;

footer {
    @import 'widgets/default-wordpress-widgets-flat';

    .widget {
        color: $widget-text-color ;

        .eltdf-widget-title-holder{
            margin-top: 40px;
            background-color: #444;
            padding: 5px 10px;
            
            .eltdf-widget-title {
                margin: 0;
            }
        }

        #wp-calendar {
            color: #fff;
        }

        &.widget_nav_menu {
            #menu-footer-custom-menu{
                li {
                    display: inline-block;
                    padding: 6px 20px;

                    a {
                        font-size: 13px;
                        text-transform: uppercase;
                        font-weight: 300;
                        letter-spacing: 0.15em;
                        font-family: $default-text-font;
                        color: #fff;

                        &:after {
                            content: '';
                            left: 0;
                            top: auto;
                            bottom: -8px;
                            height: 0;
                            width: 100%;
                            background-color: $widget-text-color;
                            padding: 0;
                            opacity: 1;
                            @include eltdfTransition(all .2s ease);
                        }

                        &:hover{
                            padding: 0;

                            &:after {
                                height: 3px;
                                @include eltdfTransition(all .2s ease);
                            }
                        }
                    }

                    &:last-child {
                        padding-bottom: 6px;
                    }
                }
            }
        }
    }

    .eltdf-footer-bottom-holder {

        .eltdf-grid-col-4 {

            &:first-child {
                text-align: left;

                @include ipad-landscape {
                    text-align: center;
                }
            }

            &:last-child {
                text-align: right;

                @include ipad-landscape {
                    text-align: center;
                }
            }

        }
    }
}